<template>
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('BatchCycle') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>

                </div>
                <div class="modal-body">
                    <tg-list :IsShowAction="false" :showCheckBox="false" ModuleName="Billing" :listType="['List']" :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="id" :HeaderText="$t('BatchProcessing')" :ListData="BatchProcessingListing" :HeaderData="Headers" :PageSize="pageSize" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:ActionButtonClick="actionButtonClick" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'created_at'">
                                <span>
                                    {{ $d(data.row.created_at) }}
                                </span>
                            </template>
                            <template v-if="data.column.COLUMN_NAME == 'batch_name'">
                                <!-- <a href="javascript:;" v-on:click="OpenBatchDetail(data)"> -->
                                    <span>{{ data.row.batch_name }}</span>
                                <!-- </a> -->
                            </template>
                            <template v-if="data.column.COLUMN_NAME == 'status'">
                                <span>
                                    <!-- {{ data.row.progressValue }}
                        {{ data.row.batch_id }} -->
                                    <div class="progress mb-1">
                                        <div id="progressBar-30" class="progress-bar progress-bar-striped bg-warning" :style="{ width: 100 + '%' }" v-if="data.row.status == 'Starting' && data.row.progressValue == 0">
                                            <span>{{ $t('Starting') }}</span>
                                        </div>
                                        <div id="progressBar-30" class="progress-bar progress-bar-striped bg-success" :style="{ width: 100 + '%' }" v-if="data.row.status == 'Completed' && data.row.progressValue == 0">
                                            <span>{{ $t('Completed') }}</span>
                                        </div>
                                        <div id="progressBar-30" class="progress-bar progress-bar-striped bg-primary" :style="{ width: 100 + '%' }" v-else-if="data.row.status == 'Generated' && data.row.progressValue == 0">
                                            <span>{{ $t('created') }}</span>
                                        </div>
                                        <div id="progressBar-30" class="progress-bar progress-bar-striped bg-danger" :style="{ width: 100 + '%' }" v-else-if="data.row.status == 'Failed' && data.row.progressValue == 0">
                                            <span>{{ $t('Failure') }}</span>
                                        </div>
                                        <div id="progressBar-30" class="progress-bar progress-bar-striped bg-warning" :style="{ width: 100 + '%' }" v-else-if="data.row.status == 'Started' && data.row.progressValue <= 0">
                                            <span>{{ $t('Inprogress') }}</span>
                                        </div>

                                        <div class="progress-bar progress-bar-striped bg-success" :style="{ width: data.row.progressValue + '%' }" v-if="data.row.progressValue > 0">
                                            <span v-if="data.row.progressValue === 100">{{ $t('Completed') }}</span>
                                            <span>{{ $t('Failure') }}</span>
                                        </div>
                                        <div id="progressBar-30" class="progress-bar progress-bar-striped bg-warning" :style="{ width: 100 + '%' }" v-else-if="data.row.status == 'Started' && data.row.progressValue <= 0">
                                            <span>{{ $t('Inprogress') }}</span>
                                        </div>

                                        <div class="progress-bar progress-bar-striped bg-success" :style="{ width: data.row.progressValue + '%' }" v-if="data.row.progressValue > 0">
                                            <span v-if="data.row.progressValue === 100">{{ $t('Completed') }}</span>
                                            <span v-else>{{ data.row.progressValue }}%</span>
                                        </div>
                                        <div id="progressBar-0" class="progress-bar progress-bar-striped bg-danger" :style="{ width: 100 + '%' }" v-else-if="data.row.status == 'Pending' && data.row.progressValue == 0">
                                            <span>{{ $t('Pending') }}</span>
                                        </div>
                                    </div>
                                </span>
                            </template>
                            <template v-if="data.column.COLUMN_NAME == 'stats'">
                                <span v-if="data.row.status == 'Generated'">
                                    --
                                </span>
                                <span v-else>
                                    {{ data.row.stats }}
                                </span>
                            </template>
                            <template v-if="data.column.COLUMN_NAME == 'record_count'">
                                <span>
                                    <a id="ancProjectDetail" :class="{ 'text-dark text-center': true, 'disabled': (data.row.record_count == 0) }" href="javascript:;" v-on:click="OpenBatchProcessDataLising(data.row.batch_id)">
                                        <em class="fa fa-file-o" aria-hidden="true"></em>
                                        <span class="noti-circle noti-orange">{{ data.row.record_count }}</span>
                                    </a>
                                </span>
                            </template>
                        </template>
                    </tg-list>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from './../../services/DataService'
import Schema from '../../views/Billing/BillingCycleSchema';
export default {
    props: {
        BatchId: {
            type: Object,
            required: true
        },
    },
    components: {},
    data() {
        return {
            reProcessDetail: {},
            reProcessIndex: 0,
            isReprocess: false,
            Headers: [{
                    COLUMN_NAME: "batch_name",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('batchname'),
                    DISPLAY_ORDER: 1,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "customer_type_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CustomerType'),
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "plan_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('Plan'),
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "station_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('StationName'),
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "created_at",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CreatedDate'),
                    DISPLAY_ORDER: 5,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "status",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('Status'),
                    DISPLAY_ORDER: 6,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "record_count",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('RecordCount'),
                    DISPLAY_ORDER: 7,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    COLUMN_NAME: "stats",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('stats'),
                    DISPLAY_ORDER: 8,
                    settings: {
                        isInSlot: true,
                    }
                }
            ],
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            searchCondition: '',
            leftSearchFields: Schema.leftsearchSchema,
            listActions: [],
            listheaderbuttons: [],
            GroupStart: 1,
            GroupEnd: 3,
            PlanId: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortExp: '',
            requestId: '',
            BatchProcessingListing: [],
            noRecords: false,
            isCreateBatch: false,
            isRecordDetail: false,
            batchName: '',
            intervalId: [],
            isDetail: false,
            BatchData: [],
            isOpenProcessData: false,
            batchProcessData: []
        }
    },
    created: async function () {
        var vm = this;
        await Schema.bindleftCommonSearchdropdown(this, 'Billing', 'CUSTOMER_TYPE', null);
        await Schema.bindleftCommonSearchdropdown(this, 'Billing', 'PLANS', null);
        await Schema.bindleftCommonSearchdropdown(this, 'Billing', 'STATION_LIST', null);
        await vm.FetchData();

    },
    methods: {
        startAllProgress: function () {
            var vm = this;
            vm.BatchProcessingListing.forEach((batch, index) => {
                var batch_status = batch.status;
                if (batch_status == 'Started')
                    vm.getProgress(index, batch.batch_id); 
            });
        },

        getProgress: function (index, batchId) {
            this.intervalId[batchId] = setInterval(async () => { 
                try {
                    var url = `batchId=${batchId}`;
                    let response = await DataService.GetBatchProgress(url);
                    if (response.data.result != null) {
                        this.BatchProcessingListing.filter(x => x.batch_id == batchId)[0].status = JSON.parse(response.data.result).status;
                        let progressValue = JSON.parse(response.data.result).progress_value;

                        if (progressValue < 100) {
                            this.BatchProcessingListing.filter(x => x.batch_id == batchId)[0].progressValue = progressValue;
                        } else {
                            this.BatchProcessingListing.filter(x => x.batch_id == batchId)[0].progressValue = 100;
                            clearInterval(this.intervalId[batchId]);
                            delete this.intervalId[batchId]; 
                        }
                        this.BatchProcessingListing.filter(x => x.batch_id == batchId)[0].stats = JSON.parse(response.data.result).stats;
                    }
                } catch (error) {
                    console.error('Error fetching progress:', error);
                    clearInterval(this.intervalId[batchId]); 
                }
            }, 15000); 
        },
        // OpenBatchDetail: async function (item) {
        //     var vm = this;
        //     vm.isDetail = true;
        //     vm.BatchData = item.row;
        // },
        OpenBatchProcessDataLising: async function (id) {
            var vm = this;
            this.BatchId = id;
            this.isOpenProcessData = !this.isOpenProcessData;
        },
        Close: function () {
            var vueObj = this;
            vueObj.$parent.isOpenProcessData = false;
        },
        FetchData: async function () {
            var vueObj = this;
            vueObj.BatchProcessingListing = [];
            var url = `search=${vueObj.searchCondition}&pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&batchId=${vueObj.BatchId ?? ''}`;
            const obj = JSON.stringify(url);
            var requestPayload = {
                objData: obj
            };
            await DataService.GetBatchCycleListing(requestPayload).then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.result.data.length > 0) {
                        vueObj.BatchProcessingListing = response.data.result.data.map(item => {
                            item.created_at = new Date(item.created_at);
                            item.progressValue = 0; // Initialize progressValue for each item
                            return item;
                        });
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.BatchProcessingListing = [];
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.BatchProcessingListing = [];
                }
            })
        },
        leftCommonSearch(value) {
            this.pageNum = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchConditionJson !== 'undefined' && value.searchConditionJson !== null) {
                this.searchCondition = value.searchConditionJson.replace('=', '');
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick(callbackfunction, item, index, event) { },

        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {

                    case 'Edit':
                        action.isVisible = true;
                        action.isDisabled = row.status == 'Generated' ? false : true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod(value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick(event) {
        },
        sortdata: function (sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.pageNum = 1;
            this.GroupStart = this.pageNum;
            this.GroupEnd = this.pageNum + 2;
            this.FetchData();
        },

    }
}
</script>
