import DataService from '../../services/DataService';

let leftsearchSchema = [
    {
        fieldName: "CustomerType",
        fieldType: "ddl-check",
        fieldIcon: "fa fa-users",
        paramName: "",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    },
];

async function bindleftCommonSearchdropdown(instance, modulename, filtername,id) {
    var vm = instance;
    if(filtername =='CUSTOMER_TYPE'){
        vm.leftSearchFields[0].listOptions=[];
        var params = `id=&moduleName=&type=${filtername}&search=&refId=`
        await DataService.GetDDLData(params).then((response) => {
            if (response.data != '') {
                if (response.data.DATA.length) {
                    response.data.DATA.forEach(element => {
                        let obj = {
                            name: element.name,
                            value: element.value
                        }
                        vm.leftSearchFields[0].listOptions.push(obj);
                    });
                }
            }
        });
    }
}

export default { leftsearchSchema, bindleftCommonSearchdropdown };